.advantages {
    padding: 95px 0 100px;

    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    &__list-item {
        margin-bottom: 20px;
        max-width: 757px;
        width: 100%;
    }
    
    &__list-item:nth-child(odd) {
        margin-right: 100px;
    }
    &__list-item:nth-child(even) {
        margin-left: 100px;
    }
}

@media (max-width: 1140px) {
    .advantages {
        &__list-item:nth-child(odd) {
            margin-right: 0px;
        }
        
        &__list-item:nth-child(even) {
            margin-left: 0px;
        }
    }
}

@media (max-width: 380px) { 
    .advantages {
        padding: 20px 0 20px;
    }
}